import React from "react";
import myImage from "../images/IMG_5630.jpeg";

export default function About() {
  return (
    <section id="about">
      <div className="container mx-auto flex px-10 py-20 md:flex-row flex-col items-center">
        <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center">
          <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-white">
            Hi, I'm Yulia.
            <br className="hidden lg:inline-block" />I love to create.
          </h1>
          <p className="mb-8 leading-relaxed">
            A bilingual Software Engineer with extensive experience in customer service.
            With a background in project, team, and time management, known for
            exceeding goals consistently. Self-driven and motivated by creating
            and executing new projects. Passionate about learning new technical
            skills and contributing to team initiatives.
          </p>
          <div className="flex justify-center">
            <a
              href="#contact"
              className="inline-flex text-white bg-green-500 border-0 py-2 px-6 focus:outline-none hover:bg-green-600 rounded text-lg"
            >
              Work With Me
            </a>
            <a
              href="#projects"
              className="ml-4 inline-flex text-gray-400 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded text-lg"
            >
              See My Past Work
            </a>
          </div>
        </div>
        <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6">
          <img
            className="object-cover object-center rounded"

            src={myImage}
          alt="my photo"/>

        </div>
      </div>
    </section>
  );
}



